import { MDXProvider } from '@mdx-js/react';
import { PageProps } from 'gatsby';
import { ReactNode } from 'react';
import { GlobalFooter } from '../global-footer';
import { GlobalHeader } from '../global-header';
import { Anchor } from '../mdx/anchor';
import { BlockQuote } from '../mdx/block-quote';
import { InlineCode } from '../mdx/inline-code';
import { Header1, Header2, Header3, Header4, Header5, Header6 } from '../mdx/headers';
import { Kbd } from '../mdx/kbd';
import { Paragraph } from '../mdx/paragraph';
import { Strong } from '../mdx/strong';
import { containerCss, contentCss } from './index.css';
import { Em } from '../mdx/em';
import { preToCodeBlock } from '@lekoarts/themes-utils';
import { CodeBlock } from '../mdx/code-block';
import { UnorderedList } from '../mdx/unordered-list';
import { List } from '../mdx/list';
import { OrderedList } from '../mdx/ordered-list';
import { Input } from '../mdx/input';
import { Hr } from '../mdx/hr';

type Props = Omit<PageProps, 'children'> & {
  children: ReactNode;
};

export const GlobalLayout = ({ children }: Props) => {
  return (
    <div className={containerCss}>
      <GlobalHeader />
      <section className={contentCss}>
        <MDXProvider
          components={{
            h1: Header1,
            h2: Header2,
            h3: Header3,
            h4: Header4,
            h5: Header5,
            h6: Header6,
            blockquote: BlockQuote,
            p: Paragraph,
            a: Anchor,
            code: InlineCode,
            strong: Strong,
            Kbd: Kbd,
            em: Em,
            ul: UnorderedList,
            ol: OrderedList,
            li: List,
            input: Input,
            pre: (preProps) => {
              const props = preToCodeBlock(preProps as Parameters<typeof preToCodeBlock>[0]);
              // if there's a codeString and some props, we passed the test

              if (props) {
                return <CodeBlock {...props} />;
              }

              // it's possible to have a pre without a code in it
              return <pre {...preProps} />;
            },
            Image: (props) => {
              return <img {...props} />;
            },
            hr: Hr,
          }}
        >
          {children}
        </MDXProvider>
      </section>

      <GlobalFooter />
    </div>
  );
};
